.regular-checkbox {
    width: 1.5em;
    height: 1.5em;
    -webkit-appearance: none;
    background-color: #fafafa;
    border: 1px solid rgb(49, 39, 131);
    ;
    box-shadow: 0 0.5px 1px rgba(0, 0, 0, 0.05), inset 0px -7.5px 5px -6px rgba(0, 0, 0, 0.05);
    padding: 4.5px;
    border-radius: 0.5em;
    display: inline-block;
    position: relative;
    vertical-align: middle;
}

.regular-checkbox:active,
.regular-checkbox:checked:active {
    box-shadow: 0 0.5px 1px rgba(0, 0, 0, 0.05), inset 0px 0.5px 1.5px rgba(0, 0, 0, 0.1);
}

.regular-checkbox:checked {
    background-color: rgb(49, 39, 131);
    border: 0.5px solid #adb8c0;
    box-shadow: 0 0.5px 1px rgba(0, 0, 0, 0.05), inset 0px -7.5px 5px -6px rgba(0, 0, 0, 0.05), inset 7.5px 5px -6px rgba(255, 255, 255, 0.1);
    color: #99a1a7;
}

.regular-checkbox:checked:after {
    content: '\2714';
    font-size: 25px;
    position: absolute;
    top: 0px;
    left: 5px;
    color: white;
}

.green-checkbox {
    width: 2rem;
    height: 2rem;
    -webkit-appearance: none;
    background-color: #fafafa;
    border: 1px solid rgb(42, 226, 42);
    box-shadow: 0 0.5px 1px rgba(0, 0, 0, 0.05), inset 0px -7.5px 5px -6px rgba(0, 0, 0, 0.05);
    padding: 4.5px;
    border-radius: 1rem;
    display: inline-block;
    position: relative;
    vertical-align: middle;
}

.green-checkbox:active,
.regular-checkbox:checked:active {
    box-shadow: 0 0.5px 1px rgba(0, 0, 0, 0.05), inset 0px 0.5px 1.5px rgba(0, 0, 0, 0.1);
}

.green-checkbox:checked {
    background-color: rgb(42, 226, 42);
    ;
    border: 0.5px solid #adb8c0;
    box-shadow: 0 0.5px 1px rgba(0, 0, 0, 0.05), inset 0px -7.5px 5px -6px rgba(0, 0, 0, 0.05), inset 7.5px 5px -6px rgba(255, 255, 255, 0.1);
    color: #99a1a7;
}

.green-checkbox:checked:after {
    content: '\2714';
    font-size: 21px;
    position: absolute;
    top: 0px;
    left: 5px;
    color: white;
}


